import { Injectable } from '@angular/core';
import { Http} from '@angular/http' ; 
import { Coupon} from '../common/coupon'; 
import { RequestOptions} from '@angular/http';
import swal from 'sweetalert2';
@Injectable({
  providedIn: 'root'
})
export class ComapnyService {
   private newCoupon : Coupon ; 
   public couponList : Coupon[];	
  constructor(private _http: Http) {
	}	

  getAllCoupons(){
  	this._http.get("https://couponsystemv1.herokuapp.com/company/getAllCoupons").subscribe(
  		(resp)=>{
  			this.couponList = resp.json();
  		})
  }

  createCoupon(Coupon){
  	console.log(Coupon);
  	this._http.post("https://couponsystemv1.herokuapp.com/company/createCoupon" , Coupon).subscribe(
  		(resp)=>{
  		 this.newCoupon = resp.json(); 
       swal ('Coupon ' + this.newCoupon.title  +   ' created');
  		})
  }

  getCouponById(id:number){
  	console.log(id);
  	return this._http.get("https://couponsystemv1.herokuapp.com/company/getCouponById/" + id);
  }

  removeCoupon(id : number){
      this._http.delete("https://couponsystemv1.herokuapp.com/company/removeCoupon/" + id).subscribe(
      (resp)=>{
       this.newCoupon = resp.json(); 
       swal ('Coupon with ID: ' + this.newCoupon +   ' removed');
      })
  }

  updateCoupon(coupon:Coupon){
    this._http.put("https://couponsystemv1.herokuapp.com/company/updateCoupon" , coupon).subscribe(
      (resp)=>{
       this.newCoupon = resp.json(); 
       swal ('Coupon with ID : ' + this.newCoupon.id  +   ' updated');
      })
  }

  getCouponByType (filter:string){
    console.log("into byType"); 
    this._http.get("https://couponsystemv1.herokuapp.com/company/sortCouponBy/TYPE/" + filter).subscribe(
      (resp)=>{
      this.couponList=resp.json();
      },
      (error)=>{
        swal(error._body); 
      })
  }

}
